html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-weight: 500;
  font-size: 16px;
  color: var(--text-color);
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
  color: inherit;
}

.site-container {
  overflow: hidden; // если используете на сайте position: sticky - уберите эту настройку
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container-fluid {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: 1850px;
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  width: 100%;
  max-width: var(--container-width);
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.primary-link {
  position: relative;
  display: block;
  padding-top: 5px;
  padding-bottom: 5px;
  transition: width 0.3s;

  &::after {
    content: "";
    position: absolute;
    left: auto;
    right: 0;
    bottom: -8px;
    display: block;
    width: 0;
    height: 2px;
    background-color: var(--accent-color);
    transition: width 0.3s;
  }

  &:hover {
    color: var(--accent-color);
    transition: width 0.3s;
  }

  &:hover::after {
    left: 0;
    right: auto;
    width: 100%;
    transition: width 0.3s;
  }
}

button svg,
a svg {
  transition: all 0.3s;
}

button:hover svg,
a:hover svg {
  fill: var(--accent-color);
  transition: all 0.3s;
}
